import https from 'https'

const getYouTubeThumbnail = (url) => {
  const cut = (str, delim) => (str.split(delim))[((str.split(delim)).length - 1)]
  const videoID = cut(cut(url, '/'), '=')
  return `https://img.youtube.com/vi/${videoID}/hqdefault.jpg`
}

const getVimeoThumbnail = (url) => {
  return new Promise(resolve => {
    const req = https.request(`https://vimeo.com/api/oembed.json?url=${encodeURIComponent(url)}`)
    req.on('response', res => {
      let str = ''
      // eslint-disable-next-line no-return-assign
      res.on('data', d => str += d.toString('utf8'))
      res.on('end', () => resolve(((JSON.parse(str)).thumbnail_url.split('_'))[0]))
    })
    req.end()
  })
}

export { getYouTubeThumbnail, getVimeoThumbnail }
