import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import leroyJohnson from '../views/leroyJohnson.vue'
import bettyLeacraft from '../views/bettyLeacraft.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/Leroy-Johnson',
    name: 'LeroyJohnson',
    component: leroyJohnson
  },
  {
    path: '/Betty-Leacraft',
    name: 'BettyLeacraft',
    component: bettyLeacraft
  }
]

const router = createRouter({
  mode: 'hash',
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
