<template>
  <!-- THIS TEMPLATE ONLY DOES VIMEO VIDEOS RN. -->
  <div class="video-wrapper">
    <div class="mbr-overlay video-overlay" :style="thumbnail"></div>
    <div>
      <div class="video-title">
        <h3 class="mbr-section-title mbr-fonts-style align-center mb-3 display-2">
          {{title}}
        </h3>
      </div>
    </div>
    <div class="play-btn-video">
      <playButton :url="video" />
    </div>
  </div>
</template>

<script>
import playButton from '@/components/main/playButton.vue'
import { getVimeoThumbnail } from '@/scripts/getThumbnail.js'

export default {
  props: ['title', 'video'],
  components: {
    playButton
  },
  data () {
    return {
      thumbnail: {}
    }
  },
  async created () {
    const thumbnailURL = await getVimeoThumbnail(this.video)
    // this.thumbnail = thumbnailURL
    this.thumbnail = {
      'background-image': `url('${thumbnailURL}')`
    }
  }
}

</script>

