import { createStore } from 'vuex'

export default createStore({
  state: {
    videoUrl: false,
    videoOpened: false,
    sidebarOpened: false
  },
  mutations: {
    setVideoUrl (state, setter) {
      state.videoUrl = setter
    },
    unsetVideoUrl (state, setter) {
      state.videoUrl = false
    },
    toggleVideo (state, setter) {
      state.videoOpened = setter || !state.videoOpened
    },
    toggleSidebar (state, setter) {
      state.sidebarOpened = setter || !state.sidebarOpened
    }
  },
  actions: {},
  modules: {}
})
