<template>
    <label for="check" class="hamburger-menu" :class="this.color" @click="handleClick">
      <input type="checkbox" disabled="disabled" :checked="isChecked" id="check" />
      <span></span>
      <span></span>
      <span></span>
    </label>
</template>

<script>
export default {
  props: ['closeOnly', 'color'],
  computed: {
    isChecked () {
      return this.closeOnly ? true : this.$store.state.sidebarOpened
    }
  },
  methods: {
    handleClick () {
      this.$emit('clickEvent')
      if (!this.closeOnly) this.$store.commit('toggleSidebar', !this.isChecked)
    }
  }
}
</script>

<style scoped>

label{
  display:flex;
  flex-direction:column;
  width:70px;
  cursor:pointer;
  opacity: 0.9;
  transition: all 0.3s ease;
}

label:hover {
  opacity: 1;
  transform: scale(1.05);
}

label span{
  background: #201c15;
  border-radius:10px;
  height:7px;
  margin: 7px 0;
  transition: .4s  cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

label.light span{
  background: white;
}

span:nth-of-type(1){
  width:50%;

}

span:nth-of-type(2){
  width:100%;
}

span:nth-of-type(3){
  width:75%;

}

input[type="checkbox"]{
  display:none;
}

input[type="checkbox"]:checked ~ span:nth-of-type(1){
  transform-origin:bottom;
  transform:rotatez(45deg) translate(8px,0px)
}

input[type="checkbox"]:checked ~ span:nth-of-type(2){

  transform-origin:top;
  transform:rotatez(-45deg)
}

input[type="checkbox"]:checked ~ span:nth-of-type(3){

  transform-origin:bottom;
  width:50%;
  transform: translate(30px,-11px) rotatez(45deg);

}

</style>
